.describeWrap {
  display: flex;
  flex-direction: row;
  font-size: 0.93em;
  margin-bottom: 1em;
}
.collapsed {
  flex-shrink: 1;
}
.expanded {
  flex: 1 0 auto;
  max-width: 100%;
}
.describe {
  align-items: center;
  display: flex;
}
.btn {
  color: var(--color-muted)!important;
  flex-shrink: 1;
  font-family: monospace, sans-serif;
  margin-right: .5em;
  min-height: 2.5em;
  min-width: 2.5em;
}