.candidateWrap {
  border: 1px solid var(--border-light);
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
.candidateWrap > * {
  flex: 1 0 auto;
  margin: .3vmax;
  min-width: 5em;
  text-align: center;
}
.badge {
  max-width: min(100vw, 30vmax);
  word-wrap: break-word;
}
.editSelect {
  border: 1px dashed!important;
}
.bulkEditText {
  overflow-y: scroll;
  min-height: 15em;
  width: 100%;
}
