.alert {
  border: 1px solid transparent;
  padding: 1em 1.5em;
  margin-bottom: .5em;
}
.basic, .basic a {
  background-color: #EEE;
  border-color: #999;
  color: #555;
}
.info, .info a {
  background-color: #EEF;
  border-color: #009;
  color: #005;
}
.positive, .positive a {
  background-color: #EFE;
  border-color: #090;
  color: #050;
}
.warn, .warn a {
  background-color: #FFD;
  border-color: #990;
  color: #550;
}
.negative, .negative a {
  background-color: #FEE;
  border-color: #900;
  color: #500;
}